const CENTER = [config.filters.latitude, config.filters.longitude] as LatLngTuple;

export const App = () => {
  const [range, setRange] = useState({
    minprice: localStorage.getItem('minprice') || 50,
    maxprice: localStorage.getItem('maxprice') || 150,
  });

  const [filters, setFilters] = useState({
    ...config.filters,
    priceRange: `${range.minprice}-${range.maxprice}`,
    checkInDate: formatDate(TODAY),
    checkOutDate: formatDate(TWO_DAY_FROM_NOW),
  });

  const [hotels, setHotels] = useState([] as Hotel[]);
  const [loading, setLoading] = useState(true);
  const [details, setDetails] = useState({} as Hotel);

  useEffect(() => {
    setLoading(true);
    // axios.get('https://picsum.photos/v2/list').then(async (response) => {
    //   const pictures = response.data.map((d: any) => d.download_url);

    //   if (accessToken.length === 0) {
    //     await getAccessToken();
    //   }

    // api.get(filters, accessToken).then((data) => {
    console.log(allHotels);
    setLoading(false);
    setHotels(
      (allHotels as Hotel[]).filter(
        (d: any) => +d.min_rates.price >= range.minprice && +d.min_rates.price <= range.maxprice,
      ),
    );
    // });
    // });
  }, [filters, setHotels, range]);

  return (
    <div style={{ position: 'relative' }}>
      <Loading loading={loading} />
      <Details details={details} onClose={() => setDetails({} as Hotel)} />
      <div
        style={{
          position: 'absolute',
          zIndex: 1000,
          backgroundColor: '#fff',
          padding: '20px',
          left: '50%',
          margin: '20px auto',
          marginLeft: '-170px',
          width: '300px',
          fontWeight: 200,
          textAlign: 'center',
          fontSize: '14px',
          boxShadow: 'rgb(0 0 0 / 70%) 0px 0px 25px 0px',
        }}
      >
        <div className="heading">Checkin - Checkout</div>
        <span style={{ fontSize: '11px' }}>
          {filters.checkInDate} - {filters.checkOutDate}
        </span>
      </div>
      <ReactTooltip />
      {!details.id && (
        <RangeInput
          minprice={range.minprice}
          maxprice={range.maxprice}
          setValues={(values: any) => {
            setRange({
              minprice: values[0],
              maxprice: values[1],
            });

            localStorage.setItem('minprice', JSON.stringify(values[0]));
            localStorage.setItem('maxprice', JSON.stringify(values[1]));
            setFilters({
              ...filters,
              priceRange: `${range.minprice}-${range.maxprice}`,
            });
          }}
        />
      )}
      <MapContainer center={CENTER} zoom={13} scrollWheelZoom={false} style={{ height: '100vh' }}>
        <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Marker position={CENTER}>
          <Tooltip direction="left" offset={[-20, 0]} opacity={1} permanent>
            <div>Bell Center</div>
          </Tooltip>
        </Marker>
        {(details.id ? hotels.filter((hotel) => hotel.id === details.id) : hotels).map((hotel) => (
          <HotelMarker
            key={hotel.id}
            hotel={hotel}
            selected={hotel.id === details.id}
            setDetails={setDetails}
          />
        ))}
      </MapContainer>
    </div>
  );
};
